import React, { useEffect, useState, useContext, useRef } from "react";
import { useParams, Link } from "react-router-dom";
import {ArrowLeftIcon, ArrowRightIcon, CloseIcon} from "./AppIcons";
import VersesContext from "../contexts/BibleContext";

import loadable from "@loadable/component";
const Chapter = loadable(() => import("./Chapter"));

function Book() {
    const { biblija, books, getBookChapters, bookChapters } = useContext(VersesContext);
    const [chapterVerses, setChapterVerses] = useState([]);

    const { book } = useParams();

    const [readWhat, setReadWhat] = useState({
        "book": book,
        "chapter": 1,
    });

    const hasChapters = useRef(false);

    // Get from the Bible only selected book (and the chapter)
    useEffect(() => {
            if (!hasChapters.current) {
                // Get verses in a book chapter
                setChapterVerses(biblija.filter(item => (
                    item.book === readWhat.book & item.chapter === Number(readWhat.chapter)
                )));
                // Get the array of a book's chapters
                getBookChapters(readWhat.book);
            }
            hasChapters.current = true;
    }, [readWhat, biblija, getBookChapters, book])

    function handleChange(event) {
        const { name, value } = event.target;

        setReadWhat(prevRead => {
            if (name === "book") {
                return {"book": value, "chapter": 1};
            }
            else {
                 return {
                    ...prevRead,
                    [name]: value
                };
            }
        });
        hasChapters.current = false; 
    }

    function handleBefore() {
        setReadWhat(prevVal => {
            return {...prevVal, chapter: Number(readWhat.chapter)-1};
        });
        hasChapters.current = false;
    }

    function handleNext() {
        setReadWhat(prevVal => {
            return {...prevVal, chapter: Number(readWhat.chapter)+1};
        });
        hasChapters.current = false;
    }

    return(
        <div>
            <div className="navbar">
                <div className="nav-item vertical-center">
                  <select id="book-select" name="book" value={readWhat.book} onChange={handleChange}>
                    {books.map(
                            book => <option key={book.eng_short} value={book.eng_short}>{book.name}</option>
                        )
                    }
                  </select>

                  <select id="chapter-select" name="chapter" value={readWhat.chapter} onChange={handleChange}>
                    {bookChapters.map(
                            chapter => <option key={chapter} value={chapter}>{chapter}</option>
                        )
                    }
                  </select>
                </div>
                <div className="close-page"><CloseIcon /></div>
            </div>

            <Chapter
                books={books}
                book={readWhat.book} chapter={readWhat.chapter}
                verses={chapterVerses}
            />

            <div className="footerbar">
                <div className="vertical-center">
                <div className="footer-item">
                    <Link to="#">
                       {(readWhat.chapter > 1) ?
                        <div id="before" onClick={handleBefore}>
                            <ArrowLeftIcon disabled={false}/>
                        </div> :
                        <div id="before">
                            <ArrowLeftIcon disabled={true} />
                        </div>
                      }
                    </Link>
                </div>
                <div className="footer-item">
                    <Link to="#">
                      {(readWhat.chapter < bookChapters.length) ?
                        <div id="next" onClick={handleNext}>
                            <ArrowRightIcon disabled={false}/>
                        </div> :
                        <div id="next">
                            <ArrowRightIcon disabled={true}/>
                        </div>
                      }
                    </Link>
                </div>
                    </div>
            </div>
        </div>
    )
}

export default Book;